import './App.css';
import { useEffect, useState } from 'react';
import Treemap from './components/Treemap';

const CLIENT_ID = process.env.REACT_APP_CLIENT_ID;
const CLIENT_SECRET = process.env.REACT_APP_CLIENT_SECRET;
let token = null;

function App(props) {
  const [tree, setTree] = useState(null);

  let updateTree = () => {
    let myHeaders = new Headers();
    myHeaders.append("Authorization", token);

    let formdata = new FormData();

    let requestOptions = {
      method: 'POST',
      headers: myHeaders,
      body: formdata,
      redirect: 'follow'
    };

    console.log('Version 0.7');

    fetch("https://idmvs.ugis.org.ua/api/dboard/get/regions", requestOptions)
    .then(response => response.json())
    .then(result => {
        let temp = JSON.parse(result).rows.filter(region => region.id_region !== 24);

        for (let i = 0; i < temp.length; i++) {
          let myHeaders = new Headers();
          myHeaders.append("Authorization", token);

          let formdata = new FormData();
          formdata.append("id_region", temp[i].id_region);

          let requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: formdata,
            redirect: 'follow'
          };

          fetch("https://idmvs.ugis.org.ua/api/dboard/get/offices", requestOptions)
            .then(response => response.json())
            .then(offices => temp[i].children = JSON.parse(offices).rows);
        }

        setTree(temp);
        console.log('Received tree: ' + tree);
    });
  }
  
  let updateToken = () => {
    let formdata = new FormData();
    formdata.append("grant_type", "client_credentials");
    formdata.append("client_secret", CLIENT_SECRET);
    formdata.append("client_id", CLIENT_ID);
    formdata.append("scope", "basic");
    formdata.append("state", "treemap1");

    let requestOptions = {
      method: 'POST',
      body: formdata,
      redirect: 'follow'
    };

    fetch("https://idmvs.ugis.org.ua/token/", requestOptions)
    .then(response => response.json())
    .then(result => {
        token = `${result.token_type} ${result.access_token}`;
        console.log('Authorized result');
        console.log(result);
        console.log('Authorized token: ' + token);
        updateTree();
    });
  }

  useEffect(() => {
    if (token == null) {
      updateToken();
    }
  });

  return (
    <div className="App">
      { tree != null ?
      <div id="treemap-box">
        <Treemap width={window.innerWidth - 50} height={window.innerHeight - 50} data={tree}/>
      </div>
      : <h1>Loading...</h1>}
    </div>
  );
}

export default App;